.solution__container{
width: 280px;
margin: 0 auto;
@media screen and (min-width: 768px){
        width: 688px;
    }
@media screen and (min-width: 1680px){
        width: 1340px;
    }

}
.solutions-title {
    margin-top: 200px;
        margin-bottom: 30px;
    font-size: 21px;
    line-height: 1.43;
    color: $text-color-dark;
    text-align: center;
    

    @media screen and (min-width:768px) {
        font-size: 26px;
        line-height: 1.27;
        margin-top: 220px;
    }

    @media screen and (min-width:1280px) {
        font-size: 42px;
        line-height: 63px;
        margin-top: 1.5;
        text-align: start;
    }

    @media screen and (min-width:1680px) {
        font-size: 46px;
        margin-top: 295px;
    }
}
.solutions-list {
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
    padding-left: 0;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
    cursor: pointer;
    

    &__item {
        margin-top: 15px;
        padding: 0px 28px 28px 28px;
        width: 280px;
        height: 267px;
        
        list-style: none;
        text-decoration: none;
        box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
        

        &:nth-child(6) {
            @media screen and(min-width:320px) and(max-width:767px) {
                margin-bottom: 51px;
            }
        }

        &:hover .solutions-list__svg {
            fill: $text-color-white;
        }

        &:hover .solutions-list__title {
            color: $text-color-white;
        }

        &:hover .solutions-list__text {
            color: $text-color-white;
        }

        &:hover {
            background-color: $accent-color;
        }

        @media screen and(min-width:768px) {
            width: 292px;

            &:nth-child(n+5) {
                @media screen and(min-width:768px) and(max-width:1279px) {
                    margin-bottom: 51px;
                }
            }
        }

        @media screen and(min-width:1280px) {
            width: 390px;
            height: 341px;
            padding-right: 50px;
            padding-left: 50px;
        }

        @media screen and(min-width:1680px) {
            width: 433px;
            height: 397px;
            padding-right: 40px;
            padding-left: 40px;
        }

    }

    &__title {
        margin-top: 0;
        margin-bottom: 15px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 15px;
            
        font-size: 13px;
        line-height: 1.54;
        color: $text-color-dark;
        font-weight: 700;
        text-align: center;      
        
        @media screen and(min-width:768px) {
            padding-left: 15px;
            padding-right: 15px;
        }

        @media screen and(min-width:1280px) {
            font-size: 18px;
            line-height: 1.62;
            padding-left: 3px;
            padding-right: 3px;
        }

        @media screen and(min-width:1680px) {
            font-size: 21px;
            line-height: 1.38;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &__text {
        
        font-size: 11px;
        line-height: 1.81;
        color: $text-color-lightgrey;
        text-align: center;

        &::before {
            content: '';
            position: relative;
            margin: 0 auto;
            width: 25px;
            height: 2px;
            background-color: $accent-color;
            display: block;
            line-height: 30px;
            
            transform: translateY(-15px);
        }

        @media screen and(min-width:768px) {
            padding-bottom: 20px;
        }

        @media screen and(min-width:1280px) {
            font-size: 15px;
            line-height: 2.06;
        }

        @media screen and(min-width:1680px) {
            font-size: 18px;
            line-height: 1.55;
            padding-left: 25px;
            padding-right: 25px;
        }
    }

    &__icon {
        position: relative;
        margin: 0 auto;
        width: 92px;
        height: 92px;
        
        display: flex;
        justify-content: center;
    }

    &__svg {
        height: 100%;
        fill: $accent-color;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: inline-block;
        text-align: center;
        background-position: center;
        background-repeat: no-repeat;

    
    }

    @media screen and(min-width:768px) {
        justify-content: space-between;
        width: 603px;
    }

    @media screen and(min-width:1280px) {
        width: 1210px;
    }

    @media screen and(min-width:1680px) {
        width: 1340px;
    }
}

.solutions-list__item:hover .solutions-list__text::before {
    background-color: $bg-color-white;
}